body[data-mountain="earth"] .shooting-star {
    background: -webkit-linear-gradient(left, transparent 0%, rgba(254, 254, 254, 0.98) 98%, #fefefe 100%);
    background: linear-gradient(to right, transparent 0%, rgba(254, 254, 254, 0.98) 98%, #fefefe 100%);
    height: 1px;
    opacity: 0.3;
    position: relative;
    top: 0;
    left: 0;
    width: 200px;
    -webkit-transition: opacity 0.8s, -webkit-transform 0.8s;
    transition: opacity 0.8s, -webkit-transform 0.8s;
    transition: opacity 0.8s, transform 0.8s;
    transition: opacity 0.8s, transform 0.8s, -webkit-transform 0.8s;
    -webkit-transform: rotate(45deg) translate(0);
            transform: rotate(45deg) translate(0);
    visibility: hidden;
}
body[data-mountain="earth"] .shooting-star.animate {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(50vw);
              transform: rotate(45deg) translate(50vw);
      visibility: visible;
}
